// базовые подключения
@use "sass:math";

@import "vars";
@import "mixins";
@import "fonts";
@import "settings";


@import url('https://fonts.googleapis.com/css2?family=Montserrat:ital,wght@0,300;0,400;0,700;1,600;1,700;1,800;1,900&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Inter:wght@400;700&display=swap');


html {
  height: 100%;
  font-family: var(--font-family, sans-serif);
  color: var(--base-black-text);
  -webkit-text-size-adjust: 100%;
}

body {
  &::after {
      content: '';
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100vh;
      background-color: rgba(0, 0, 0, 0);
      transition: background-color 0.3s ease;
      z-index: -1;
    }
  .lock & {
    overflow: hidden;
    touch-action: none;
    overscroll-behavior: none;

  }
  .lock &::after {
    background-color: rgba(0, 0, 0, 0.6);
    z-index: 10;
  }

  // Сайт замонтирован
  .loaded & {

  }
}

.wrapper {
  min-height: 100%;

  background: url('../img/bg-footer.png') bottom center / 100% auto no-repeat;


   @media (max-width: toEm(1300)) {
    	grid-template-columns: toRem(235) 1fr;
   }
  @media (min-width: toEm(1025)) {
      display: grid;
      grid-template-columns: toRem(287) 1fr;
  }
}


.page {
  overflow: hidden;
  padding: toRem(36) toRem(50) toRem(50) toRem(40);
  min-height: 100%;
  display: flex;
  flex-direction: column;
  @media (max-width: toEm(1300)){
    padding: toRem(36) toRem(20) toRem(50);
  }
  @media (max-width: toEm(1024)) {
    padding: toRem(25) toRem(20) toRem(50);
  }
}

.main {
  padding-top: toRem(40);
}



