.card {
  position: relative;
  padding: toRem(24) toRem(30) toRem(30);
  border-radius: toRem(12);
  
  @media (max-width: toEm(1300)) {
    padding: toRem(20);
  }

  &__icon {
    position: absolute;
    top: toRem(20);
    right: toRem(30);
    &--ethereum {
      width: toRem(24);
      height: toRem(40);
      fill: #c6b7ec;
    }
    &--bnb {
      width: toRem(44);
      height: toRem(40);
      fill: #b4c7ed;
    }
    &--tron {
      width: toRem(40);
      height: toRem(40);
      fill: #a9cfe7;
    }
    &--bitcoin {
      width: toRem(34);
      height: toRem(34);
      fill: #e5d4a6;
    }
  }

  &__title {
    margin-bottom: toRem(28);
    font-weight: 500;
    font-size: toRem(20);
    color: var(--black-text);
    @media (max-width: toEm(768)) {
      font-size: toRem(18);
    }
    &--invite {
      margin-bottom: toRem(10);
    }
  }
  &__text {
    margin-bottom: toRem(13);
    font-size: toRem(11);
    line-height: 130%;
  }

  &__amount {
    margin-bottom: toRem(20);
    display: flex;
    gap: toRem(30);

  }

  &__amount-item {}

  &__amount-title {
    font-size: toRem(12);
    margin-bottom: toRem(5);
  }

  &__amount-value {
    font-size: toRem(14);
  }

  &__actions {
    display: flex;
    gap: toRem(8);
   
  }
  &__action {
    min-height: toRem(36);
    font-size: toRem(11);
  }
}

.card--purple {
  background-color: var(--light-purple-bg);
}

.card--blue {
  background-color: var(--blue-bg);
}
.card--blue-light {
  background-color: var(--light-blue-bg);
}
.card--beige {
  background-color: var(--beige-bg);
}