.sidebar {
  padding: toRem(46) toRem(36) toRem(50) toRem(36);
  &__logo {
    margin-bottom: toRem(47);
  }

  position: relative;
  height: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;

  @media (max-width: toEm(1300)) {
    padding: toRem(46) toRem(20) toRem(50);
  }
  @media (min-width: toEm(1024)){
    border-right: 1px solid #f2f2f2;
    border-image: linear-gradient(to bottom,
      #f2f2f2,
      rgba(0, 0, 0, 0)) 1 100%;
  }

  @media (max-width: toEm(1024)) {
    position: fixed;
    z-index: 1000;
    width: toRem(250);
    height: 100%;
    left: -100%;
    top: 0;
    overflow: auto;
    background-color: #fff;
    padding: toRem(50) toRem(15) toRem(15);
    transition: left 0.3s;

  .menu-open & {
    left: 0;
  }
  }
}


.nav {
  margin-bottom: toRem(192);
  // margin-bottom: toRem(120);
  display: flex;
  flex-direction: column;
  width: 100%;
  @media (max-width: toEm(1024)){
    margin-bottom: toRem(120);
  }


  &__title {
    margin-bottom: toRem(14);
    padding-left: toRem(31);
    font-size: toRem(10);
    line-height: 100%;
    letter-spacing: 0.03em;
    text-transform: uppercase;
    @media (max-width: toEm(1024)){
      font-size: toRem(12);
   }
  }

  &__list {
    display: flex;
    flex-direction: column;
    gap: toRem(2);
    &:not(:last-child) {margin-bottom: toRem(53);}
  }

  &__item {
    display: flex;
    align-items: center;
    justify-content: start;

  }

  &__link {
    padding-left: toRem(30);
    min-height: toRem(40);
    border-radius: 6px;
    width: 100%;
    display: flex;
    align-items: center;
    gap: toRem(18);
    font-size: toRem(12);
    line-height: 100%;
    background: transparent;
    transition: color 0.3s ease, background 0.3s ease;
    position: relative;
    z-index: 100;

    @media (max-width: toEm(1300)) {
      padding-left: toRem(20);
    }
    @media (max-width: toEm(1024)){
      font-size: toRem(14);
   }

    &:before {
      border-radius: inherit;
      background: linear-gradient(45deg, #bfabd6 0%, #6a12cb 100%);
      content: '';
      display: block;
      height: 100%;
      position: absolute;
      top: 0;
      left: 0;
      opacity: 0;
      width: 100%;
      z-index: -100;
      transition: opacity .3s ease;
    }
    &.active{
      outline: none;
      color: var(--base-white);
    }

    &.active .nav__link-icon{
      fill: var(--base-white);
    }

    &.active::before {
    opacity: 1;
    }

    &:focus-visible,
    &:active {
      outline: none;
      color: var(--base-white);
    }

    &:focus-visible::before,
    &:active::before {
      opacity: 1;
    }

    @media (any-hover: hover) {
      &:hover{
        color: var(--base-white);
      }
    }

    @media (any-hover: hover) {
      &:hover::before {
        opacity: 1;
      }
    }

    &:focus-visible .nav__link-icon,
    &:active .nav__link-icon {
      outline: none;
      fill: var(--base-white);
    }

    @media (any-hover: hover) {
      &:hover .nav__link-icon {
        fill: var(--base-white);
      }
    }
  }

  &__link-icon {
    width: toRem(20);
    height: toRem(20);
    transition: fill 0.3s ease;
  }

  &__link-text {}
}

.invite {
  //margin-top: auto;
  padding: toRem(49) toRem(24) toRem(22);
  position: relative;
  display: flex;
  flex-direction: column;
  align-items: center;
  background-color: var(--blue-bg);
  border-radius: 12px;

  &__image {
    position: absolute;
    top: toRem(-88);
    width: toRem(118);
    height: toRem(118);
    img {
      max-width: 100%;
    }
  }

  &__title {
    margin-bottom: toRem(16);
    font-family: var(--second-family);
    font-size: toRem(20);
    line-height: 100%;
    color: #000;
  }

  &__text {
    margin-bottom: toRem(20);
    font-size: toRem(12);
    line-height: 110%;
    text-align: center;
    color: #000;
  }

  &__button {
    min-height: toRem(40);
  }
}


