

.card-balance {
  padding: toRem(24) toRem(10) toRem(21) toRem(34);
  max-width: toRem(350);
  border-radius: toRem(12);
  @media (max-width: toEm(1300)){
      padding: toRem(20) toRem(5) toRem(20) toRem(20);
  }

  &__title {
    margin-bottom: toRem(8);
    font-weight: 500;
    font-size: toRem(20);
    color: var(--black-text);
    @media (max-width: toEm(768)){
      font-size: toRem(18);
    }
  }

  &__graph {
    display: grid;
    grid-template-columns: 1fr toRem(159);
    align-items: end;
      @media (max-width: toEm(1300)) {
        grid-template-columns: 1fr toRem(140);
      }
      @media (max-width: toEm(768)) {
        grid-template-columns: 1fr toRem(120);
      }
  }

  &__list {
    padding-top: toRem(10);
    display: flex;
    flex-direction: column;
    row-gap: toRem(10);
  }

  &__value {
    font-weight: 600;
    font-size: toRem(18);
    line-height: 100%;

    @media (max-width: toEm(768)) {
      font-size: toRem(14);
    }
  }

  &__image {
    width: 100%;
  }

  &--purple {
    background-color: var(--light-purple-bg);
    color: var(--violet)
  }
  &--blue {
    background-color: var(--blue-bg);
    color: var(--blue-text)
  }
}

