.invites {
  &__list {
  display: grid;
  gap: var(--gap-card);
  grid-template-columns: minmax(toRem(350), toRem(566)) minmax(toRem(300), toRem(350));

  @media (max-width: toEm(1300)) {
    gap: var(--gap-card-mob);
  }

  @media (max-width: toEm(768)) {
    grid-template-columns: minmax(toRem(320), toRem(566));
  }
}

}
