.table {
  max-width: toRem(1266);
  display: flex;
  flex-direction: column;
  overflow: auto;

  @media (max-width: toEm(768)){
    margin-right: -20px;
    margin-left: -20px;
    padding-left: 20px;
    padding-right: 20px;

      // &::-webkit-scrollbar {
      //   display: none;
      // }
  }
  &--account .table__items {
    grid-template-columns: minmax(toRem(80), toRem(160)) minmax(toRem(50), toRem(124)) minmax(toRem(50), toRem(160)) minmax(toRem(80), toRem(173)) minmax(toRem(80), toRem(186)) minmax(toRem(80), toRem(137)) minmax(toRem(80), toRem(143)) minmax(toRem(80), toRem(121));
  }
  &--partners .table__items {
    grid-template-columns: minmax(toRem(150), toRem(240)) minmax(toRem(100), toRem(220)) minmax(toRem(100), toRem(230)) minmax(toRem(80), toRem(220)) minmax(toRem(50), toRem(180)) minmax(toRem(80), toRem(120));
  }

  &--wallets .table__items {
    grid-template-columns: minmax(toRem(100), toRem(246)) minmax(toRem(100), toRem(293)) minmax(toRem(100), toRem(245)) minmax(toRem(100), toRem(255)) minmax(toRem(80), toRem(160));
  }

  &--investments .table__items {
    grid-template-columns: minmax(toRem(80), toRem(160)) minmax(toRem(50), toRem(124)) minmax(toRem(50), toRem(160)) minmax(toRem(80), toRem(173)) minmax(toRem(80), toRem(186)) minmax(toRem(80), toRem(137)) minmax(toRem(80), toRem(143)) minmax(toRem(80), toRem(121));
  }

  &--performance .table__items {
  
    grid-template-columns: minmax(toRem(40), toRem(131)) minmax(toRem(100), toRem(164)) minmax(toRem(70), toRem(111)) minmax(toRem(70), toRem(111)) minmax(toRem(70), toRem(111));
  }
    &--performance {
  max-width: toRem(653);
    }
 &--performance .table__row {
  min-width: toRem(330);
 }

  &__row {
    position: relative;
    min-width: toRem(700);
    border-right: 1px solid #f2f2f2;
    border-left: 1px solid #f2f2f2;
    background-color: #fff;
    &:first-child {
      border-top: 1px solid #f2f2f2;
      padding-top: toRem(11);
      border-radius: 12px 12px 0 0;
      }
    &:last-child {
      border-bottom: 1px solid #f2f2f2;
      padding-bottom: toRem(33);
      border-radius: 0px 0px 12px 12px;
    }

    &:not(:last-child):after {
      position: absolute;
      content: '';
      left: 0;
      right: 0;
      bottom: 0;
      width: calc(100% - 60px);
      margin: 0 auto;
      height: toRem(1);
      background-color: #f5f5f5;
        @media (max-width: toEm(1300)) {
            width: calc(100% - 30px);
        }
    }
  }

  &__items {
    padding-left: toRem(30);
    padding-right: toRem(30);
    height: toRem(52);
    display: grid;
    align-items: center;
    font-weight: 500;
    font-size: toRem(14);
    @media (max-width: toEm(1300)){
      padding-left: toRem(15);
      padding-right: toRem(15);
      font-size: toRem(13);
    }

    &--title {
      font-size: toRem(12);
      font-weight: 400;
      line-height: 100%;
    }
  }

  &__item {
    &:not(:last-child) {padding-right: toRem(10);}
  }

  &__item-status {
    display: flex;
    align-items: center;
    justify-content: center;
    height: toRem(24);
    border-radius: 4px;
    color: var(--base-white);
    width: toRem(85);
    background: var(--blue);
    &--active {
      width: toRem(73);
      background: var(--light-green);
    }
  }
}

