.switch-btn {
  display: inline-block;
  width: toRem(52);
  height: toRem(28);
  border-radius: 99px;
  background: rgba(120, 120, 128, 0.16);
  z-index: 0;
  margin: 0;
  padding: 0;
  border: none;
  cursor: pointer;
  position: relative;
}

.switch-btn::after {
  content: "";
  height: toRem(28);
  width: toRem(28);
  border-radius: 18px;
  background: #fff;
  top: 0;
  left: 0;
  transition-duration: 300ms;
  border: 0.5px solid rgba(0, 0, 0, 0.04);
  box-shadow: 0 1px 2px 0 rgba(0, 0, 0, 0.2), 0 0px 0px 0 rgba(0, 0, 0, 0.1);
  position: absolute;
  z-index: 1;
  transition: left 0.3s ease 0s;
}

.switch-on {
  background: rgba(120, 120, 128, 0.16);
}

.switch-on::after {
  left: 25px;
  background:linear-gradient(45deg, #bfabd6 0%, rgb(108, 18, 204) 100%);
  box-shadow: none;
  border: none;
}