// если вы хотите использовать sass-переменные - удалите root
// colors


:root {

  --font-family: "Poppins", sans-serif;
  --second-family: "Quantify", sans-serif;

  --content-width: 1830px;
  --container-offset: 15px;
  --container-width: calc(var(--content-width) + (var(--container-offset) * 2));

  --base-black-text: #212121;
  --base-white: #fff;
  --black-text: #0b0b0b;
  --gray-text: #2e384d;
  --violet: #6a12cb;
  --blue-text: #2575fc;

  --light-green: #70ce98;
  --blue: #7090ce;

  --blue-bg: #ecf2fe;
  --light-blue-bg: #ecf8fe;
  --light-purple-bg: #f1ecfe;
  --beige-bg: #fef9ec;

  --side-width: 287px;
  --gap-card: 30px;
  --gap-card-mob: 20px;
  // --green: #39d24e;

}
