
.icon-menu {
  display: none;

  @media (max-width: toEm(1024)) {
    display: block;
    position: relative;
    width: toRem(24);
    height: toRem(16);

    @media (any-hover: none) {
      cursor: default;
    }

    span,
    &::before,
    &::after {
      content: "";
      right: 0;
      position: absolute;
      width: 100%;
      height: toRem(2);
      background-color: #000;
    }

    &--burger {
      &::before {
        top: 0;
      }
      &::after {
        bottom: 0;
      }
      span {
        top: calc(50% - toRem(1));
      }
    }

    &--open {
      position: absolute;
      right: toRem(10);
      top: toRem(10);

      &::before {
        top: calc(50% - toRem(1));
        transform: rotate(-45deg);
      }
      &::after {
        bottom: calc(50% - toRem(1));
        transform: rotate(45deg);
      }
    }
  }
}