    .progressbar {
      display: grid;
      grid-template-columns: repeat(3, 1fr);
      gap: toRem(12);
      width: 100%;
    }

    .progressbar li {
      height: 5px;
      background-color: #d9d9d9;
      border-radius: 20px;
    }

    .progressbar li.active {
      background-color: #6c15cb;
    }