
.header {
  display: flex;
  column-gap: toRem(10);
  align-items: start;

  @media (max-width: toEm(1024)){
    flex-wrap: wrap;
    row-gap: toRem(30);
    align-items: center;
    justify-content: space-between;
  }

  &__menu {
   
    @media (min-width: toEm(1200)){
        display: none;
    }
  }

  &__block {
    flex-grow: 1;
      @media (max-width: toEm(1024)) {
        flex-basis: 100%;
        order: 2;
      }
  }

  &__title {
    margin-bottom: toRem(10);
    font-family: var(--second-family);
    font-size: toRem(40);
    @media (max-width: toEm(768)){
      font-size: toRem(30);
    }
  }

  &__text {
    font-weight: 300;
    font-size: toRem(15);
  }

  &__actions {
    display: flex;
    align-items: center;
      @media (max-width: toEm(1024)) {
        order: 1;
        gap: toRem(20);
      }
      @media (max-width: toEm(768)) {
        gap: toRem(15);
      }
  }

  &__search,
  &__notification {
      @media (min-width: toEm(1024)) {
        margin-right: toRem(42);
      }
    
  }

  &__search:focus-visible,
  &__notification:focus-visible,
  &__search:active,
  &__notification:active {
    outline: none;
    fill: var(--violet);
  }

  &__search-icon {
    width: toRem(18);
    height: toRem(18);
    transition: fill 0.3s ease;
  }


  &__notification-icon {
    width: toRem(20);
    height: toRem(20);
    transition: fill 0.3s ease;
  }

  &__search-icon:hover,
  &__notification-icon:hover {
    fill: var(--violet)
  }


&__lang {
  position: relative;
  min-width: toRem(50);
  &.active .header__lang-btn {
    color: var(--violet)
  }
  @media (min-width: toEm(1024)) {
    margin-right: toRem(28);
  }

  &.active .arrow {
    fill: var(--violet)
  }
}

&__lang-btn {
  // padding-bottom: toRem(10);
  display: inline-flex;
  align-items: center;
  justify-content: center;
  position: relative;
  display: flex;
  align-items: center;
  column-gap: toRem(5);
  font-weight: 500;
  &.rotate .header__lang-arrow {
    transform: rotate(180deg);
  }
}

&__lang-list {
  padding: toRem(10) 0;
  min-width: toRem(70);
  position: absolute;
  z-index: 1000;
  top: 100%;
  left: 0;
  box-shadow: 0px 2px 10px rgba(0, 0, 0, 0.2);
  background-color: #fff;
  border-radius: 6px;
  opacity: 0;
  visibility: hidden;
  pointer-events: none;
  transform: translate(0px, -10px);
  transition: opacity 0.3s ease, transform 0.3s ease;

  &.open {
    opacity: 1;
    visibility: visible;
    pointer-events: auto;
    transform: translate(0px, 5px);
  }
}

&__lang-item {
  &:not(:last-child) {
    margin-bottom: toRem(10);
  }
  padding: 0 toRem(8);

  &:hover {
    cursor: pointer;
    text-decoration: underline;
  }

  &.hidden {
    display: none;
  }
}
}

.user {
  display: flex;
  align-items: center;
  column-gap: toRem(12);
  cursor: pointer;
  &__avatar {
    padding: toRem(6);
    width: toRem(36);
    height: toRem(36);
    flex: 0 0 toRem(36);
    background: #cfe2f3;
    border-radius: 10px;
    img {
      width: 100%;
    }
  }

  &__info {
    text-align: start;
  }

  &__name {
    font-weight: 500;
    font-size: toRem(12);
    line-height: 130%;

  }

  &__id {
    font-weight: 300;
    font-size: toRem(11);
    color: #818181;
  }
}

.header__lang-btn:focus-visible,
.header__user:focus-visible,
.header__lang-btn:active,
.header__user:active {
  outline-offset: 5px;
  outline-color: var(--violet);
  color: var(--violet);
}


.header__lang-btn:hover,
.header__user:hover {
  color: var(--violet);
}

.header__lang-btn:hover .arrow,
.header__user:hover .arrow {
  fill: var(--violet);
}

.arrow {
  width: toRem(10);
  height: toRem(7);
  transition: transform 0.3s ease, fill 0.3s ease;
}


