.form * {
  outline: none;

}

.form {
  padding: toRem(20) toRem(34) toRem(24);
  border-radius: 12px;
  background-color: var(--light-purple-bg);

  @media (max-width: toEm(768)){
    padding: toRem(15);
  }

  &--blue {
    background-color: var(--blue-bg);
  }

  &__label {
    display: block;
    margin-bottom: toRem(5);
    font-size: toRem(12);
  }

  &__button {
    font-size: toRem(12);
    width: toRem(167);
    height: toRem(40);
  }
}


.input {
  padding-left: toRem(20);
  padding-right: toRem(20);
  display: flex;
  align-items: center;
  gap: toRem(17);
  background-color: #fff;
  border-radius: 5px;
  overflow: hidden;
  transition: box-shadow 0.3s ease;

  &--network {
    gap: toRem(9);
  }

    @media (max-width: toEm(768)){
      padding-left: toRem(8);
      padding-right: toRem(8);
      gap: toRem(7);
    }

  &__icon {
    fill: #dadada;
    flex:0 0 toRem(16);
    width: toRem(16);
    height: toRem(16);
    &--name {
      width: toRem(16);
      height: toRem(17);
    }

    &--password {
      width: toRem(16);
      height: toRem(16);
    }

    &--trc, &--copy {
      flex: 0 0 toRem(24);
      width: toRem(24);
      height: toRem(20);
    }

    &--eye,
    &--eye-off {
      pointer-events: none;
      width: toRem(16);
      height: toRem(16);
      transition: fill 0.3s ease 0s;
    }

    &--eye {
      display: none;
    }
    &--copy {
      width: toRem(24);
      height: toRem(20);
      fill: var(--violet);
    }
  }

  &__control {
    flex-grow: 1;
    width: 100%;
    height: toRem(50);
    width: 100%;
    font-size: toRem(13);
    outline: none;
    overflow: hidden;
    text-overflow: ellipsis;
    &--network {
      color: #dadada;
    }
    @media (max-width: toEm(768)){
      height: toRem(40);
    }
  }

  &__icon-btn.show {
    .input__icon--eye {
      display: block;
    }
    .input__icon--eye-off {
      display: none;
    }
  }
  &__icon-btn:hover,
  &__icon-btn:active,
  &__icon-btn:focus-visible {
    .input__icon--eye,
    .input__icon--eye-off{
      fill: var(--violet)
    }
  }

  &__icon-btn:hover,
  &__icon-btn:active,
  &__icon-btn:focus-visible {
    .input__icon--copy {
      fill: rgb(106, 18, 203, .7);
    }
  }

  &__text {
    font-size: toRem(13);
    color: #dadada;
  }
}

.input:has(.input__control:focus) {
  box-shadow: 0 0 5px var(--violet);
}


.checkbox.error .checkbox__label::before {
  box-shadow: 0 0 5px red;
}

.checkbox__input {
   position: absolute;
  -webkit-appearance: none;
     -moz-appearance: none;
          appearance: none;
}

.checkbox__input:checked+.checkbox__label::after {
  transform: scale(1);
}
.checkbox__input:focus-visible + .checkbox__label:before {
  box-shadow: 0 0 5px var(--violet);
  // outline: 1px solid red;
}

.checkbox__label {
  font-size: toRem(11);
  display: flex;
  align-items: center;
  position: relative;
  cursor: pointer;
}

.checkbox__label::before {
  content: "";
  align-self: flex-start;
  flex: 0 0 toRem(24);
  height: toRem(24);
  background-color: #fff;
  border-radius: 6px;
  margin: 0px toRem(9) 0px 0px;
}

.checkbox__label::after {
  transition: transform 0.3s ease 0s;
  content: "";
  width: toRem(16);
  height: toRem(16);
  position: absolute;
  top: 4px;
  left: 4px;
  background: linear-gradient(45deg, #bfabd6 0%, #6a12cb 100%);
  border-radius: 6px;
  transform: scale(0);
}

.checkbox__label a {
  text-decoration: underline;
  color: var(--violet);
  &:hover {
    text-decoration: none;
  }
}







