.authentication {
  max-width: toRem(806);
  &__progressbar {
    margin-bottom: toRem(30);
  }

  &__title {}

  &__form {}
}


.authentication-form {
  position: relative;
  &__title {
    margin-bottom: toRem(30);
    line-height: 120%;
    @media (max-width: toEm(768)){
      font-size: toRem(15);
    }
  }

  &__text {
    margin-bottom: toRem(38);
    max-width: toRem(500);
    line-height: 120%;
    @media (max-width: toEm(768)) {
      max-width: 60%;
      font-size: toRem(15);
    }
    @media (max-width: toEm(420)){
        max-width: 100%;
    }
  }

  &__title-big {
    margin-bottom: toRem(23);
    font-family: var(--second-family);
    font-size: toRem(48);
    @media (max-width: toEm(768)) {
      font-size: toRem(30);
    }
  }

  &__items {
    margin-bottom: toRem(26);
    max-width: toRem(459);
  }

  &__item {
    &:not(:last-child) {
      margin-bottom: toRem(18);
    }
  }

  &__icon {
    position: absolute;
    width: toRem(156);
    height: toRem(156);
    right: toRem(58);
    top: 50%;
    transform: translate(0px,-50%);
    background: url('../../img/icons_check.svg') 0 0 no-repeat;
    @media (max-width: toEm(1300)){
      right: toRem(40);
    }
    @media (max-width: toEm(768)){
      width: toRem(100);
      height: toRem(100);
      background-size: toRem(100);
    }
    @media (max-width: toEm(420)){
      display: none;
    }
  }

  &__body {
    margin-bottom: toRem(24);
    display: grid;
    grid-template-columns: 40% 50%;
    align-items: center;
    gap: toRem(46);
      @media (max-width: toEm(1024)){
        gap: toRem(30);
      }
      @media (max-width: toEm(768)){
        grid-template-columns: auto;
      }
  }

  &__image {
    width: 100%;
      @media (max-width: toEm(768)){
        max-width: toRem(240);
      }
    
    img {
      width: 100%;
    }
  }

  &__info {}

  &__info-text {
    font-size: toRem(14);
    line-height: 1.4;
    margin-bottom: toRem(2);
  }

  &__info-label {
    margin-bottom: toRem(5);
    span {
      color: var(--blue-text);
    }
  }

  &__info-key {
    display: flex;
    align-items: center;
    gap: toRem(10);
  }

  &__info-code {
    font-weight: 700;
    font-size: toRem(20);
    color: var(--blue-text);
    @media (max-width: toEm(1024)){
      font-size: toRem(16);
    }
    @media (max-width: toEm(768)){
      font-size: toRem(15);
    }
  }

  &__info-icon {
    cursor: pointer;
    width: toRem(18);
    height: toRem(20);
    fill: var(--blue-text);
    transition: fill 0.3s ease;
    @media (max-width: toEm(1024)){
      width: toRem(14);
      height: toRem(16);
    }
  }

  &__info-icon:hover {
    fill: rgb(37, 117, 252, 0.7);
  }
  

  &__button {}
}
