.wallets {

  &__list {
    display: grid;
    gap: var(--gap-card);
    grid-template-columns: repeat(auto-fit, minmax(toRem(320), toRem(350)));
  
    @media (max-width: toEm(1300)) {
      grid-template-columns: repeat(2, minmax(toRem(200), toRem(350)));
      gap: var(--gap-card-mob);
    }
  
    @media (max-width: toEm(768)) {
      grid-template-columns: repeat(2, minmax(toRem(200), toRem(300)));
    }
  
    @media (max-width: toEm(576)) {
      grid-template-columns: minmax(toRem(200), toRem(350));
    }
  }

  &__card {}
}

