

// Блок со спойлерами
.spollers {
  @media (min-width: toEm(1300)){
    grid-template-columns:  minmax(toRem(300), toRem(650)) minmax(toRem(300), toRem(650));
    // grid-template-columns: 50% 50%;
  }
  
  display: grid;
  gap: toRem(20);
}


.spollers__column {

}
.spollers__list {


}
// Спойлер
.spollers__item {
  &:not(:last-child) {margin-bottom: toRem(20);}

  border-radius: toRem(15);
  background: #f3f3f3;
  overflow: hidden;

}
// Заголовок спойлера
.spollers__title {
  padding: toRem(26) toRem(26) toRem(26) toRem(40);
  display: flex;
  justify-content: space-between;
  align-items: center;
  gap: toRem(20);
	width: 100%;
	cursor: default;
	text-align: left;
  background-color: transparent;

  font-size: toRem(15);
  font-weight: 600;
  line-height: 120%;
    @media (max-width: toEm(768)){
      padding: toRem(20)
    }

	._spoller-init & {
		cursor: pointer;

		&._spoller-active {
        .spollers__icon {
          transform: rotate(-180deg);
          fill: var(--violet);
        }
		}
	}
}

.spollers__icon {
  flex: 0 0 toRem(16);
  width: toRem(16);
  height: toRem(9);
  fill: var(--blue-text);
  transition: transform 0.2s ease;
}

// Контент спойлера
.spollers__body {
  padding: toRem(0) toRem(62) toRem(26) toRem(40);
	border-radius:0 0  toRem(15) toRem(15);
  background: #f3f3f3;
	@media (max-width: toEm(768)){
    padding: 0 toRem(56) toRem(20) toRem(20);
  }
}
