.register {
  
  min-height: 100%;
  background: none;
  display: grid;
  
  @media (min-width: toEm(768)){
    grid-template-columns: 1fr toRem(680);
  }

  &__left-side {
    position: relative;
    display: none;
    @media (min-width: toEm(768)){
      display: block;
    &:before {
      position: absolute;
      width: 100%;
      height: 100%;
      top: 0;
      left: 0;
      content: '';
      background: url('../img/bg.png') center /cover no-repeat;
    }
    }
  }

  &__content {
    min-height: 100%;
    position: relative;
    background: #ecf2fe;
    display: grid;
    align-items: center;
  }

  &__inner {
    padding-top: toRem(80);
    padding-bottom: toRem(80);
    max-width: toRem(400);
    margin: 0 auto;
    display: grid;
    justify-items: center;
    @media (max-width: toEm(768)){
        padding: toRem(20);
    }
  }

  &__link-back {
    position: absolute;
    top: toRem(46);
    left: toRem(40);
    display: flex;
    align-items: center;
    gap: toRem(10);
    font-size: toRem(13);
    &:hover,
    &:active,
    &:focus-visible {
      outline: none;
      .register__link-arrow {
        fill: var(--violet);
      }
    }
    @media (max-width: toEm(768)){
      top: toRem(20);
      left: toRem(20);
    }
  }

  &__link-arrow {
    width: toRem(16);
    height: toRem(9);
    fill: var(--blue-text);
    transform: rotate(90deg);
    transition: fill 0.3s ease;
  }

  &__logo {
      margin-bottom: toRem(100);
      @media (max-width: toEm(1300)) {
        margin-bottom: toRem(50);
      }
  }

  &__title {
    margin-bottom: toRem(34);
    font-family: var(--second-family);
    font-size: toRem(37);
  }

  &__text {
    margin-bottom: toRem(25);
    font-weight: 300;
    font-size: toRem(14);
    line-height: 120%;
  }

  &__actions {
    margin-bottom: toRem(19);
    width: 100%;
    display: flex;
    flex-direction: column;
    gap: toRem(12);
  }

  &__action {
    padding: toRem(5) toRem(20) toRem(5) toRem(49);
    display: grid;
    grid-template-columns: toRem(24) 1fr;
    align-items: center;
    gap: toRem(68);
    width: 100%;
    height: toRem(48);
    background: #fff;
    border-radius: 500px;
    color: #2e384d;
    font-size: toRem(14);
    transition: background 0.3s ease;

    &--email{
      margin-bottom: toRem(22);
      display: flex;
      align-items: center;
      justify-content: center;
      padding: toRem(20);
    }
    &:hover {
      background: rgb(0, 0, 0, 0.1);
    }
    @media (max-width: toEm(768)){
      padding: toRem(5) toRem(20);
      gap: toRem(30);
    }
  }

  &__form {
    margin-bottom: toRem(24);
    width: 100%;
    display: flex;
    flex-direction: column;
    gap: toRem(16);
  }
  
  &__button {
    min-height: toRem(60);
    @media (max-width: toEm(768)){
      min-height: toRem(50);
    }
  }

  &__action-icon {}

  &__action-text {}

  &__or {
    margin-bottom: toRem(21);
    text-align: center;
  }

  &__or-line {
    width: 100%;
    margin-bottom: toRem(21);
    text-align: center;
    position: relative;
    &:before {
      position: absolute;
      width: 100%;
      height: 100%;
      top: 50%;
      left: 0;
      content: '';
      height: 1px;
      background-color: #a3b4d8;
    }
    span {
      background-color: #ecf2fe;
      position: relative;
      padding: 0 toRem(20);
    }
  }

  &__login {
    &:not(:last-child) {margin-bottom: toRem(22);}
    font-size: toRem(14);
    line-height: 130%;
  }

  &__link-login {
    font-weight: 600;
    color: var(--violet);
    text-decoration: underline;
    &:hover {
      text-decoration: none;
    }
  }

  &__check {
    display: grid;
    grid-template-columns: 1fr auto;
    justify-content: space-between;
    align-items: center;
    gap: toRem(10);
  }

  &__remember {
    font-size: toRem(12);
    color: var(--violet);
    text-decoration: underline;
    &:hover {
      text-decoration: none;
    }
  }

  &__list {
    display: flex;
    align-items: center;
    gap: toRem(12);
  }

  &__item {
  
    
  }
  &__item-link {
      width: toRem(40);
      height: toRem(40);
      display: flex;
      align-items: center;
      justify-content: center;
      background-color: #4481eb;
      border-radius: 50%;
      transition: background-color 0.3s ease;
  }

  &__item-link:hover {
    background-color: #1d5cc7;
  }

  &__item-icon {
    width: toRem(20);
    height: toRem(20);
    fill: #fff;
  }
}

