.wallets-actions {
  max-width: toRem(613);
  padding: toRem(40) toRem(60) toRem(47) toRem(32);
  border-radius: 12px;
  background-color: var(--light-purple-bg);



  @media (max-width: toEm(768)) {
    padding: toRem(15);
  }
  &__title {
    margin-bottom: toRem(28);
    font-family: var(--second-family);
    font-size: toRem(36);
    @media (max-width: toEm(768)){
      margin-bottom: toRem(20);
      font-size: toRem(30);
    }
  }

  &__descr {
    margin-bottom: toRem(30);
    line-height: 140%;
    @media (max-width: toEm(768)) {
      margin-bottom: toRem(20);
    }
  }

  &__text {
    @media (max-width: toEm(768)){
      font-size: toRem(15);
    }
    &--success {
      max-width: toRem(421);
    }
  }

  &__text--blue-bold {
    font-weight: 700;
    color: #2575fc;
  }

  &__text--violet {
    font-weight: 700;
    color: var(--violet);
  }

  &__text-link {
    text-decoration: underline;
    color: #2575fc;
    &:hover {
      text-decoration: none;
    }
  }

  &__inputs {
    &:not(:last-child) {
      margin-bottom: toRem(25);
    }
  }

  &__item {
    position: relative;
    &:not(:last-child) {
      margin-bottom: toRem(25);
    }
  }
  &__label-bottom {
    position: absolute;
    right: 0;
    bottom: toRem(-16);
    font-size: toRem(11);
    color: #2575fc;
    text-decoration: underline;
  }

  &__image {
    &:not(:last-child) {
      margin-bottom: toRem(25);
    }
    display: block;
    border-radius: 10px;
    overflow: hidden;
    width: toRem(196);
    height: toRem(196);
  }


  &__actions {
    display: flex;
    align-items: center;
    gap: toRem(35);
  }

  &__cancel {
    text-decoration: underline;
    font-size: toRem(12);
    &:hover {
      text-decoration: none;
    }
  }
}
