.button {
  display: inline-flex;
  width: 100%;
  align-items: center;
  justify-content: center;
  border-radius: 6px;
  background: linear-gradient(45deg, #bfabd6 0%, #6a12cb 100%);
  color: var(--base-white);
  font-weight: 600;
  position: relative;
  z-index: 4;

&:before{
  border-radius: inherit;
  background: linear-gradient(45deg, #bfabd6 0%, #985cd8 100%);
  content: '';
  display: block;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
  opacity: 0;
  width: 100%;
  z-index: -100;
  transition: opacity .3s ease;
}

&--transparent {
  border: 1px solid #212121;
  background: transparent;
  color: var(--base-black-text);
  transition: background 0.3s ease;
}

&--transparent::before{
  display: none;
}

  &--transparent:focus-visible,
  &--transparent:active {
    outline: none;
    background: rgb(0, 0, 0, 0.1);
  }
  @media (any-hover: hover) {
    &--transparent:hover {
       background: rgb(0, 0, 0, 0.1);
    }
  }

&__icon {
  width: 16px;
  height: toRem(16);
  fill: var(--base-white);
  margin-right: toRem(4);
}

&__text {
  font-weight: 600;
  font-size: toRem(11);
}

  &:focus-visible,
  &:active {
    outline: none;
  }
    &:focus-visible::before,
    &:active::before {
      opacity: 1;
    }

  @media (any-hover: hover) {
    &:hover::before {
      opacity: 1;
    }
  }

  &--fw {
    width: 100%;
  }
}

